@font-face {
  font-family: 'Elaine D02';
  font-style: normal;
  font-weight: normal;
  src:
          local('Elaine D02'),
          url(./fonts/Elaine_D02/Elaine_D02.eot?#iefix) format('embedded-opentype'),
          url(./fonts/Elaine_D02/Elaine_D02.woff) format('woff'),
          url(./fonts/Elaine_D02/Elaine_D02.ttf) format('truetype'),
          url(./fonts/Elaine_D02/Elaine_D02.svg) format('svg');
}
